* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

li {
  text-align: center;
  margin-bottom: 10px;
  display: flex;
}

span {
  margin-right: 10px;
  font-size: large;
  font-weight: 500;
}

.boldspan {
  margin-left: 10px;
  margin-right: 10px;
  font-size: large;
  font-weight: 800;
}

img {
  margin-right: 10px;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 85px 0 0;
  display: grid;
}

.photo__card {
  position: relative;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo__image:hover {
  transform: scale(1.03);
}

.photo__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.photo__link:hover {
  cursor: zoom-in;
}

.search-form {
  z-index: 5;
  width: 100%;
  background-color: rgba(17, 127, 230, .404);
  justify-content: center;
  margin-bottom: 10px;
  padding: 20px;
  display: flex;
  position: fixed;
}

.search:hover, .search:focus {
  background-color: #188ce8;
  border-color: #188ce8;
}

.search-form__icon:hover, .search-form__icon:focus {
  background-color: #188ce8;
}

.load-more {
  margin: auto auto 10px;
  padding: 8px 12px;
  display: block;
}

.load-more.hidden {
  display: none;
}

.load-more:hover, .load-more:focus {
  color: #f0f8ff;
  background-color: #188ce8;
  border-color: #188ce8;
}

.search-form__icon {
  width: 20px;
  height: 20px;
  color: currentColor;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.icon-spinner, .search.is-click .icon-search {
  display: none;
}

.info {
  display: flex;
}

.overlay {
  width: 100%;
  height: 20%;
  background-color: rgba(63, 81, 181, .604);
  transition: transform .25s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.photo__card:hover .overlay {
  transform: translateY(0);
}

.overlay p {
  color: #fff;
  align-items: center;
  margin: 0;
  padding: 10px;
  font-size: 12px;
  display: block;
}

.info-item b {
  margin-right: 5px;
  display: inline-block;
}

